"use client";

import Link from "next/link";
import { usePathname } from "next/navigation";
import { cn } from "@/lib/utils";
import {
    LayoutDashboard,
    Car,
    Users,
    BarChart3,
    Settings,
    Globe,
    MessageSquare,
    Bell,
    ChevronDown,
    LogOut,
    Menu,
    X,
} from "lucide-react";
import { useState } from "react";

const navigation = [
    { name: "Dashboard", href: "/dashboard", icon: LayoutDashboard },
    { name: "Inventory", href: "/dashboard/inventory", icon: Car },
    { name: "Leads", href: "/dashboard/leads", icon: Users },
    { name: "Analytics", href: "/dashboard/analytics", icon: BarChart3 },
    { name: "Reviews", href: "/dashboard/reviews", icon: MessageSquare },
    { name: "Website", href: "/dashboard/website", icon: Globe },
    { name: "Settings", href: "/dashboard/settings", icon: Settings },
];

export default function DashboardLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    const pathname = usePathname();
    const [sidebarOpen, setSidebarOpen] = useState(false);

    return (
        <div className="min-h-screen bg-app-bg flex">
            {/* Mobile sidebar backdrop */}
            {sidebarOpen && (
                <div
                    className="fixed inset-0 bg-black/50 z-40 lg:hidden"
                    onClick={() => setSidebarOpen(false)}
                />
            )}

            {/* Sidebar */}
            <aside
                className={cn(
                    "fixed inset-y-0 left-0 z-50 w-64 bg-app-card border-r border-app-border transform transition-transform duration-300 lg:translate-x-0 lg:static lg:z-auto",
                    sidebarOpen ? "translate-x-0" : "-translate-x-full"
                )}
            >
                <div className="flex flex-col h-full">
                    {/* Logo */}
                    <div className="h-16 flex items-center justify-between px-4 border-b border-app-border">
                        <Link href="/dashboard" className="flex items-center gap-2">
                            <div className="h-8 w-8 rounded-lg bg-gradient-to-br from-app-accent to-blue-400 flex items-center justify-center">
                                <span className="text-white font-bold text-sm">D</span>
                            </div>
                            <span className="font-display font-semibold text-lg text-white">
                                DealerSite<span className="text-app-accent">Pro</span>
                            </span>
                        </Link>
                        <button
                            onClick={() => setSidebarOpen(false)}
                            className="lg:hidden p-1 text-app-muted hover:text-white"
                        >
                            <X className="w-5 h-5" />
                        </button>
                    </div>

                    {/* Navigation */}
                    <nav className="flex-1 p-4 space-y-1">
                        {navigation.map((item) => {
                            const isActive = pathname === item.href;
                            return (
                                <Link
                                    key={item.name}
                                    href={item.href}
                                    onClick={() => setSidebarOpen(false)}
                                    className={cn(
                                        "flex items-center gap-3 px-3 py-2.5 rounded-xl text-sm font-medium transition-all duration-200",
                                        isActive
                                            ? "bg-app-accent text-white"
                                            : "text-app-muted hover:text-white hover:bg-app-border/50"
                                    )}
                                >
                                    <item.icon className="w-5 h-5" />
                                    {item.name}
                                </Link>
                            );
                        })}
                    </nav>

                    {/* Dealer info */}
                    <div className="p-4 border-t border-app-border">
                        <div className="flex items-center gap-3 p-3 rounded-xl bg-app-border/30">
                            <div className="w-10 h-10 rounded-full bg-gradient-to-br from-app-accent to-blue-400 flex items-center justify-center text-white font-bold">
                                A
                            </div>
                            <div className="flex-1 min-w-0">
                                <p className="text-sm font-medium text-white truncate">ABC Motors</p>
                                <p className="text-xs text-app-muted truncate">abc-motors.dealersitepro.com</p>
                            </div>
                            <ChevronDown className="w-4 h-4 text-app-muted" />
                        </div>
                    </div>
                </div>
            </aside>

            {/* Main content */}
            <div className="flex-1 flex flex-col min-w-0">
                {/* Header */}
                <header className="h-16 flex items-center justify-between px-4 lg:px-6 border-b border-app-border bg-app-card/50 sticky top-0 z-30 backdrop-blur-xl">
                    <div className="flex items-center gap-4">
                        <button
                            onClick={() => setSidebarOpen(true)}
                            className="lg:hidden p-2 text-app-muted hover:text-white"
                        >
                            <Menu className="w-5 h-5" />
                        </button>
                        <h1 className="font-display font-semibold text-lg text-white">
                            {navigation.find((item) => item.href === pathname)?.name || "Dashboard"}
                        </h1>
                    </div>

                    <div className="flex items-center gap-3">
                        <button className="relative p-2 rounded-xl text-app-muted hover:text-white hover:bg-app-border/50 transition-colors">
                            <Bell className="w-5 h-5" />
                            <span className="absolute top-1 right-1 w-2 h-2 bg-app-error rounded-full" />
                        </button>
                        <Link
                            href="/"
                            className="p-2 rounded-xl text-app-muted hover:text-white hover:bg-app-border/50 transition-colors"
                        >
                            <LogOut className="w-5 h-5" />
                        </Link>
                    </div>
                </header>

                {/* Page content */}
                <main className="flex-1 p-4 lg:p-6">
                    {children}
                </main>
            </div>
        </div>
    );
}
